import React, { useEffect, useState } from "react";
import {useHistory, useParams} from 'react-router-dom';
import styled from "styled-components";
import './Detail.scss';

// 사용자 정의 styled component 정의
// >> class 명을 지정하지 않음.
let _div = styled.div`
    padding: 20px;
    width:100%;
`
let _h4 = styled.h4`
    font-size:25px;
    color : ${props => props.color}
`


function Detail(props){

    let mytimer;

    const [showAlert, setShowAlert] = useState(true);

    // console.log(props.shoes);
    let history = useHistory();
    let {id} = useParams();

    // let _shoe = props.shoes.filter((item)=>{
    //     return item.id === Number(id);
    // });

    // let _shoe = props.shoes.find((x)=>x.id==id);
    let _shoe = props.shoes.find((x)=>{
        return x.id==id;
    });
    

    useEffect(()=>{
        mytimer = setTimeout(()=>{
            setShowAlert(false)
        }, 2000);

    },[showAlert]); //==> showAlert가 변경될때만 실행한다.
    // console.log(_shoe[0]);
    
    useEffect(()=>{
        // 해당 컴포넌트가 사라졌을 때 (unmount 됏을때) 호출
        return function(){
            // alert("bye~~ Detail Component");
            alert("bye~~ Detail Component");
            clearTimeout(mytimer);
        }
    },[]); //==> showAlert가 변경될때만 실행한다.

    return (
        <div className="container">
            <_div>
                <_h4 color='blue'>상세페이지</_h4>
             </_div>

             {
                 showAlert === true ? <div className="my-alert2">
                 <p>재고가 얼마남지 않았습니다.</p>
              </div> : null
             }
            
            <div className="row">
                <div className="col-md-6">
                <img src={`https://codingapple1.github.io/shop/shoes${Number(_shoe.id)+1}.jpg`} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                <h4 className="pt-5">{_shoe.title}</h4>
                <p>{_shoe.content}</p>
                <p>{_shoe.price}원</p>
                <button className="btn btn-danger">주문하기</button> 
                <button className="btn btn-primary" onClick={(e)=>{
                    // history.goBack();
                    history.push("/");
                    // history.push("/main");
                }}>뒤로가기</button> 
                </div>
            </div>
        </div> 
    );
}

export default Detail;