// import logo from './logo.svg';
import React, {useEffect, useState} from 'react';
import './App.css';
import {Navbar, Container, Nav, Jumbotron, Button} from 'react-bootstrap';
import {shoesData, userName} from './data';
import Shoes from './components/Shoes';
import {Link, Route, Switch} from 'react-router-dom';
import Detail from './components/Detail';
import axios from 'axios';

function App() {

  const [shoes, setShoes] = useState(shoesData)
  return (
    <div className="App">
        <>
        <Navbar bg="dark" variant="dark">
          <Container>
          <Navbar.Brand href="#home">MyShop</Navbar.Brand>
          <Nav className="me-auto">

          <Nav.Link as={Link} to="/">Home</Nav.Link>
          <Nav.Link as={Link} to="/detail">Detail</Nav.Link>

            {/* <Nav.Link href="#pricing">Pricing</Nav.Link> */}
          </Nav>
          </Container>
        </Navbar> 

        <Switch>

          <Route path="/" exact>
            <Jumbotron className="background">
              <h1>20% Season off{userName}</h1>
              <p>
                This is a simple hero unit, a simple jumbotron-style component for calling
                extra attention to featured content or information.
              </p>
              <p>
                <Button variant="primary">Learn more</Button>
              </p>
            </Jumbotron>
            <div className="container">
            <div className="row">
              {
                shoes.map((s)=>{
                  return (
                    <Shoes key={s.id} id={s.id} title={s.title} content={s.content} price={s.price}/>
                  );

                })
              }
            </div>
            <button className="btn btn-primary" onClick={()=>{
              axios.get('http://localhost:3000/data/data2.json')
              .then((result)=>{
                if ((result.status) ===200){
                  let data = result.data;
                  setShoes([...shoes, ...data]);
                }
              })
              .catch(()=>{
                console.log("실패했어요");

              });

            }}>더보기</button>
          </div>

          </Route>

          <Route path="/detail/:id" >
              <Detail shoes={shoes}/>
          </Route>

        </Switch>

      </>
    </div>
  );
}

export default App;
